import { useState } from 'react';
import { 
  Card, 
  Button, 
  Space, 
  Typography, 
  Alert,
  message,
  Spin,
  Divider
} from 'antd';
import { 
  DeleteOutlined,
  WarningOutlined,
  InfoCircleOutlined,
  ClearOutlined
} from '@ant-design/icons';
import apiService from '../services/apiService';
import { 
  PageHeader,
  DeletionResourceCard, 
  SummaryStatCard, 
  DeletionOrderTags, 
  ScriptDisplayCard,
  ConfirmationModal 
} from '../components';

const { Text, Paragraph } = Typography;

const ResourceDeletionPage = () => {
  const [loading, setLoading] = useState(false);
  const [deletionPlan, setDeletionPlan] = useState(null);
  const [bashScript, setBashScript] = useState('# Click "Generate Script" button to create deletion script');
  const [backendAvailable, setBackendAvailable] = useState(true);
  const [isCleanModalVisible, setIsCleanModalVisible] = useState(false);
  const [confirmText, setConfirmText] = useState('');
  const [isCleaningResources, setIsCleaningResources] = useState(false);
  const [isStateFileEmpty, setIsStateFileEmpty] = useState(false);

  // Generate bash script from deletion plan
  const generateBashScript = (plan) => {
    if (!plan || !plan.resources || plan.resources.length === 0) {
      return '# No resources to delete';
    }

    const script = [];
    script.push('#!/bin/bash');
    script.push('');
    script.push('# AWS Resource Deletion Script');
    script.push(`# Generated at: ${new Date().toLocaleString()}`);
    script.push(`# Total resources: ${plan.totalResources}`);
    script.push(`# Region: ${plan.region || 'N/A'}`);
    script.push('');
    script.push('# WARNING: This script will DELETE resources from your AWS account!');
    script.push('# Review carefully before executing.');
    script.push('# Resources are ordered by dependency (delete children before parents)');
    script.push('');
    script.push('set -e  # Exit on error');
    script.push('');
    script.push('# Disable AWS CLI pager to prevent interactive prompts');
    script.push('export AWS_PAGER=""');
    script.push('');

    if (plan.warnings && plan.warnings.length > 0) {
      script.push('# WARNINGS:');
      plan.warnings.forEach(warning => {
        script.push(`# ⚠️  ${warning}`);
      });
      script.push('');
    }

    script.push('# Deletion sequence (in order):');
    plan.deletionOrder.forEach((resourceId, index) => {
      const resource = plan.resources.find(r => r.id === resourceId);
      if (resource) {
        const displayName = resource.properties?.resourceName || resource.properties?.name || resource.properties?.resourceId || resource.id;
        script.push(`# ${index + 1}. ${displayName} (${resource.type})`);
      }
    });
    script.push('');
    script.push('echo "Starting resource deletion..."');
    script.push('echo ""');
    script.push('');

    // Generate deletion commands for each resource
    plan.deletionOrder.forEach((resourceId, index) => {
      const resource = plan.resources.find(r => r.id === resourceId);
      if (!resource) return;

      const displayName = resource.properties?.resourceName || resource.properties?.name || resource.properties?.resourceId || resource.id;
      script.push(`# Step ${index + 1}: Delete ${displayName}`);
      script.push(`echo "Deleting ${resource.type}: ${displayName} (${resourceId})..."`);
      
      const deleteCommands = generateDeleteCommand(resource, plan.region);
      if (deleteCommands) {
        script.push(deleteCommands);
        
        // Add wait command for resources that need time to delete
        const waitCommand = generateWaitCommand(resource, plan.region);
        if (waitCommand) {
          script.push(waitCommand);
        }
        
        script.push(`echo "✓ Deleted ${resourceId}"`);
      } else {
        script.push(`echo "⚠️  Manual deletion required for ${resourceId}"`);
      }
      
      script.push('echo ""');
      script.push('');
    });

    script.push('echo "All resources deleted successfully!"');

    return script.join('\n');
  };

  // Generate wait command for resources that need time to be fully deleted
  const generateWaitCommand = (resource, planRegion) => {
    const region = planRegion || 'ap-southeast-1';
    const resourceId = resource.properties?.resourceId || resource.id;

    switch (resource.type) {
      case 'nat_gateway':
        const natId = resource.properties?.natGatewayId || resourceId;
        return `echo "Waiting for NAT Gateway to be deleted..."\naws ec2 wait nat-gateway-deleted --nat-gateway-ids ${natId} --region ${region} || echo "Wait completed or timed out"`;
      
      case 'ec2_instance':
        return `echo "Waiting for EC2 instance to terminate..."\naws ec2 wait instance-terminated --instance-ids ${resourceId} --region ${region} || echo "Wait completed or timed out"`;
      
      case 'load_balancer':
      case 'load_balancer_v2':
      case 'alb':
      case 'nlb':
        return `echo "Waiting for Load Balancer to be deleted..."\nsleep 30  # Load balancers take time to delete`;
      
      case 'rds_instance':
      case 'db_instance':
        return `echo "Waiting for RDS instance to be deleted..."\naws rds wait db-instance-deleted --db-instance-identifier ${resource.properties?.resourceName || resource.properties?.name} --region ${region} || echo "Wait completed or timed out"`;
      
      case 'rds_cluster':
      case 'db_cluster':
        return `echo "Waiting for RDS cluster to be deleted..."\naws rds wait db-cluster-deleted --db-cluster-identifier ${resource.properties?.resourceName || resource.properties?.name} --region ${region} || echo "Wait completed or timed out"`;
      
      case 'internet_gateway':
        return `echo "Waiting for Internet Gateway detachment..."\nsleep 5  # Give time for IGW to detach`;
      
      case 'vpc_endpoint':
        return `echo "Waiting for VPC Endpoint to be deleted..."\nsleep 10`;
      
      case 'transit_gateway':
      case 'transit_gateway_attachment':
        return `echo "Waiting for Transit Gateway resource to be deleted..."\nsleep 15`;
      
      case 'ecs_service':
        return `echo "Waiting for ECS service to be deleted..."\naws ecs wait services-inactive --cluster ${resource.properties?.clusterName} --services ${resource.properties?.resourceName || resource.properties?.name} --region ${region} || echo "Wait completed or timed out"`;
      
      case 'eks_cluster':
        return `echo "Waiting for EKS cluster to be deleted (this may take several minutes)..."\nsleep 60`;
      
      case 'eks_nodegroup':
        return `echo "Waiting for EKS nodegroup to be deleted..."\nsleep 30`;
      
      default:
        return null; // No wait needed for this resource type
    }
  };

  // Generate AWS CLI delete command based on resource type
  const generateDeleteCommand = (resource, planRegion) => {
    const region = planRegion || 'ap-southeast-1';
    // Use resourceId from properties if available, otherwise use id
    const resourceId = resource.properties?.resourceId || resource.id;
    const resourceName = resource.properties?.resourceName || resource.properties?.name;
    
    switch (resource.type) {
      // ===== EC2 & VPC Resources =====
      case 'ec2_instance':
        return `aws ec2 terminate-instances --instance-ids ${resourceId} --region ${region}`;
      
      case 'security_group':
        return `aws ec2 delete-security-group --group-id ${resourceId} --region ${region}`;
      
      case 'subnet':
        const subnetId = resource.properties?.subnetId || resourceId;
        return `aws ec2 delete-subnet --subnet-id ${subnetId} --region ${region}`;
      
      case 'vpc':
        const vpcId = resource.properties?.vpcId || resourceId;
        return `aws ec2 delete-vpc --vpc-id ${vpcId} --region ${region}`;
      
      case 'internet_gateway':
        const igwId = resource.properties?.internetGatewayId || resourceId;
        if (resource.properties?.vpcId) {
          return `aws ec2 detach-internet-gateway --internet-gateway-id ${igwId} --vpc-id ${resource.properties.vpcId} --region ${region} && aws ec2 delete-internet-gateway --internet-gateway-id ${igwId} --region ${region}`;
        }
        return `aws ec2 delete-internet-gateway --internet-gateway-id ${igwId} --region ${region}`;
      
      case 'nat_gateway':
        const natId = resource.properties?.natGatewayId || resourceId;
        return `aws ec2 delete-nat-gateway --nat-gateway-id ${natId} --region ${region}`;
      
      case 'route_table':
        const routeTableId = resource.properties?.routeTableId || resourceId;
        return `aws ec2 delete-route-table --route-table-id ${routeTableId} --region ${region}`;
      
      case 'network_acl':
        return `aws ec2 delete-network-acl --network-acl-id ${resourceId} --region ${region}`;
      
      case 'elastic_ip':
      case 'eip':
        return `aws ec2 release-address --allocation-id ${resourceId} --region ${region}`;
      
      case 'network_interface':
      case 'eni':
        return `aws ec2 delete-network-interface --network-interface-id ${resourceId} --region ${region}`;
      
      case 'vpc_peering_connection':
        return `aws ec2 delete-vpc-peering-connection --vpc-peering-connection-id ${resourceId} --region ${region}`;
      
      case 'vpn_gateway':
        return `aws ec2 delete-vpn-gateway --vpn-gateway-id ${resourceId} --region ${region}`;
      
      case 'customer_gateway':
        return `aws ec2 delete-customer-gateway --customer-gateway-id ${resourceId} --region ${region}`;
      
      case 'vpn_connection':
        return `aws ec2 delete-vpn-connection --vpn-connection-id ${resourceId} --region ${region}`;
      
      case 'transit_gateway':
        return `aws ec2 delete-transit-gateway --transit-gateway-id ${resourceId} --region ${region}`;
      
      case 'transit_gateway_attachment':
        return `aws ec2 delete-transit-gateway-attachment --transit-gateway-attachment-id ${resourceId} --region ${region}`;
      
      case 'vpc_endpoint':
        return `aws ec2 delete-vpc-endpoints --vpc-endpoint-ids ${resourceId} --region ${region}`;
      
      case 'key_pair':
        return `aws ec2 delete-key-pair --key-name ${resourceName} --region ${region}`;
      
      case 'placement_group':
        return `aws ec2 delete-placement-group --group-name ${resourceName} --region ${region}`;
      
      case 'snapshot':
        return `aws ec2 delete-snapshot --snapshot-id ${resourceId} --region ${region}`;
      
      case 'volume':
      case 'ebs_volume':
        return `aws ec2 delete-volume --volume-id ${resourceId} --region ${region}`;
      
      case 'ami':
      case 'image':
        return `aws ec2 deregister-image --image-id ${resourceId} --region ${region}`;
      
      // ===== Load Balancing =====
      case 'load_balancer':
      case 'elb':
        // For ALB/NLB, use ARN; for Classic ELB, use name
        const lbArn = resource.properties?.loadBalancerArn || resource.properties?.resourceArn || resourceId;
        if (lbArn.startsWith('arn:')) {
          return `aws elbv2 delete-load-balancer --load-balancer-arn ${lbArn} --region ${region}`;
        }
        return `aws elb delete-load-balancer --load-balancer-name ${resourceName} --region ${region}`;
      
      case 'load_balancer_v2':
      case 'alb':
      case 'nlb':
        const lbArn2 = resource.properties?.loadBalancerArn || resource.properties?.resourceArn || resourceId;
        return `aws elbv2 delete-load-balancer --load-balancer-arn ${lbArn2} --region ${region}`;
      
      case 'target_group':
        const tgArn = resource.properties?.targetGroupArn || resource.properties?.resourceArn || resourceId;
        return `aws elbv2 delete-target-group --target-group-arn ${tgArn} --region ${region}`;
      
      case 'listener':
        const listenerArn = resource.properties?.resourceArn || resourceId;
        return `aws elbv2 delete-listener --listener-arn ${listenerArn} --region ${region}`;
      
      // ===== Auto Scaling =====
      case 'auto_scaling_group':
      case 'asg':
        const asgName = resource.properties?.autoScalingGroupName || resourceName || resourceId;
        return `aws autoscaling delete-auto-scaling-group --auto-scaling-group-name ${asgName} --force-delete --region ${region}`;
      
      case 'launch_configuration':
        return `aws autoscaling delete-launch-configuration --launch-configuration-name ${resourceName} --region ${region}`;
      
      case 'launch_template':
        const ltId = resource.properties?.launchTemplateId || resourceId;
        return `aws ec2 delete-launch-template --launch-template-id ${ltId} --region ${region}`;
      
      // ===== S3 =====
      case 's3_bucket':
        return `aws s3 rb s3://${resource.properties?.bucketName || resourceName} --force --region ${region}`;
      
      // ===== Lambda =====
      case 'lambda_function':
        return `aws lambda delete-function --function-name ${resource.properties?.functionName || resourceName} --region ${region}`;
      
      case 'lambda_layer':
        return `aws lambda delete-layer-version --layer-name ${resourceName} --version-number ${resource.properties?.version || 1} --region ${region}`;
      
      case 'lambda_event_source_mapping':
        return `aws lambda delete-event-source-mapping --uuid ${resourceId} --region ${region}`;
      
      // ===== DynamoDB =====
      case 'dynamodb_table':
        return `aws dynamodb delete-table --table-name ${resource.properties?.tableName || resourceName} --region ${region}`;
      
      case 'dynamodb_global_table':
        return `aws dynamodb delete-global-table --global-table-name ${resourceName} --region ${region}`;
      
      // ===== RDS =====
      case 'rds_instance':
      case 'db_instance':
        return `aws rds delete-db-instance --db-instance-identifier ${resourceName} --skip-final-snapshot --region ${region}`;
      
      case 'rds_cluster':
      case 'db_cluster':
        return `aws rds delete-db-cluster --db-cluster-identifier ${resourceName} --skip-final-snapshot --region ${region}`;
      
      case 'db_subnet_group':
        const dbSubnetGroupName = resource.properties?.dbSubnetGroupName || resourceName || resourceId;
        return `aws rds delete-db-subnet-group --db-subnet-group-name ${dbSubnetGroupName} --region ${region}`;
      
      case 'db_parameter_group':
        return `aws rds delete-db-parameter-group --db-parameter-group-name ${resourceName} --region ${region}`;
      
      case 'db_security_group':
        return `aws rds delete-db-security-group --db-security-group-name ${resourceName} --region ${region}`;
      
      case 'db_snapshot':
        return `aws rds delete-db-snapshot --db-snapshot-identifier ${resourceName} --region ${region}`;
      
      // ===== ElastiCache =====
      case 'elasticache_cluster':
        return `aws elasticache delete-cache-cluster --cache-cluster-id ${resourceName} --region ${region}`;
      
      case 'elasticache_replication_group':
        return `aws elasticache delete-replication-group --replication-group-id ${resourceName} --region ${region}`;
      
      case 'elasticache_subnet_group':
        return `aws elasticache delete-cache-subnet-group --cache-subnet-group-name ${resourceName} --region ${region}`;
      
      // ===== ECS =====
      case 'ecs_cluster':
        return `aws ecs delete-cluster --cluster ${resourceName} --region ${region}`;
      
      case 'ecs_service':
        return `aws ecs delete-service --cluster ${resource.properties?.clusterName} --service ${resourceName} --force --region ${region}`;
      
      case 'ecs_task_definition':
        return `aws ecs deregister-task-definition --task-definition ${resourceName} --region ${region}`;
      
      // ===== EKS =====
      case 'eks_cluster':
        return `aws eks delete-cluster --name ${resourceName} --region ${region}`;
      
      case 'eks_nodegroup':
        return `aws eks delete-nodegroup --cluster-name ${resource.properties?.clusterName} --nodegroup-name ${resourceName} --region ${region}`;
      
      case 'eks_fargate_profile':
        return `aws eks delete-fargate-profile --cluster-name ${resource.properties?.clusterName} --fargate-profile-name ${resourceName} --region ${region}`;
      
      // ===== ECR =====
      case 'ecr_repository':
        return `aws ecr delete-repository --repository-name ${resourceName} --force --region ${region}`;
      
      // ===== CloudFormation =====
      case 'cloudformation_stack':
      case 'cfn_stack':
        return `aws cloudformation delete-stack --stack-name ${resourceName} --region ${region}`;
      
      case 'cloudformation_stack_set':
        return `aws cloudformation delete-stack-set --stack-set-name ${resourceName} --region ${region}`;
      
      // ===== IAM =====
      case 'iam_role':
        return `aws iam delete-role --role-name ${resource.properties?.roleName || resourceName}`;
      
      case 'iam_policy':
        return `aws iam delete-policy --policy-arn ${resourceId}`;
      
      case 'iam_user':
        return `aws iam delete-user --user-name ${resourceName}`;
      
      case 'iam_group':
        return `aws iam delete-group --group-name ${resourceName}`;
      
      case 'iam_instance_profile':
        return `aws iam delete-instance-profile --instance-profile-name ${resourceName}`;
      
      case 'iam_access_key':
        return `aws iam delete-access-key --access-key-id ${resourceId} --user-name ${resource.properties?.userName}`;
      
      // ===== CloudWatch =====
      case 'cloudwatch_alarm':
        return `aws cloudwatch delete-alarms --alarm-names ${resourceName} --region ${region}`;
      
      case 'cloudwatch_log_group':
        return `aws logs delete-log-group --log-group-name ${resourceName} --region ${region}`;
      
      case 'cloudwatch_dashboard':
        return `aws cloudwatch delete-dashboards --dashboard-names ${resourceName} --region ${region}`;
      
      // ===== SNS =====
      case 'sns_topic':
        return `aws sns delete-topic --topic-arn ${resourceId} --region ${region}`;
      
      case 'sns_subscription':
        return `aws sns unsubscribe --subscription-arn ${resourceId} --region ${region}`;
      
      // ===== SQS =====
      case 'sqs_queue':
        return `aws sqs delete-queue --queue-url ${resource.properties?.queueUrl || resourceId} --region ${region}`;
      
      // ===== API Gateway =====
      case 'api_gateway':
      case 'rest_api':
        return `aws apigateway delete-rest-api --rest-api-id ${resourceId} --region ${region}`;
      
      case 'api_gateway_v2':
      case 'http_api':
      case 'websocket_api':
        return `aws apigatewayv2 delete-api --api-id ${resourceId} --region ${region}`;
      
      case 'api_gateway_stage':
        return `aws apigateway delete-stage --rest-api-id ${resource.properties?.apiId} --stage-name ${resourceName} --region ${region}`;
      
      case 'api_gateway_deployment':
        return `aws apigateway delete-deployment --rest-api-id ${resource.properties?.apiId} --deployment-id ${resourceId} --region ${region}`;
      
      // ===== CloudFront =====
      case 'cloudfront_distribution':
        return `# CloudFront distributions must be disabled before deletion\n# aws cloudfront update-distribution --id ${resourceId} --if-match <ETag> --distribution-config <config with Enabled=false>\n# aws cloudfront delete-distribution --id ${resourceId} --if-match <ETag>`;
      
      // ===== Route53 =====
      case 'route53_hosted_zone':
        return `aws route53 delete-hosted-zone --id ${resourceId}`;
      
      case 'route53_record_set':
        return `# Route53 record sets require change batch - manual deletion recommended`;
      
      // ===== Secrets Manager =====
      case 'secret':
      case 'secretsmanager_secret':
        return `aws secretsmanager delete-secret --secret-id ${resourceId} --force-delete-without-recovery --region ${region}`;
      
      // ===== Systems Manager =====
      case 'ssm_parameter':
        return `aws ssm delete-parameter --name ${resourceName} --region ${region}`;
      
      case 'ssm_document':
        return `aws ssm delete-document --name ${resourceName} --region ${region}`;
      
      // ===== KMS =====
      case 'kms_key':
        return `aws kms schedule-key-deletion --key-id ${resourceId} --pending-window-in-days 7 --region ${region}`;
      
      case 'kms_alias':
        return `aws kms delete-alias --alias-name ${resourceName} --region ${region}`;
      
      // ===== Kinesis =====
      case 'kinesis_stream':
        return `aws kinesis delete-stream --stream-name ${resourceName} --region ${region}`;
      
      case 'kinesis_firehose':
        return `aws firehose delete-delivery-stream --delivery-stream-name ${resourceName} --region ${region}`;
      
      // ===== Glue =====
      case 'glue_database':
        return `aws glue delete-database --name ${resourceName} --region ${region}`;
      
      case 'glue_table':
        return `aws glue delete-table --database-name ${resource.properties?.databaseName} --name ${resourceName} --region ${region}`;
      
      case 'glue_crawler':
        return `aws glue delete-crawler --name ${resourceName} --region ${region}`;
      
      case 'glue_job':
        return `aws glue delete-job --job-name ${resourceName} --region ${region}`;
      
      // ===== Step Functions =====
      case 'stepfunctions_state_machine':
        return `aws stepfunctions delete-state-machine --state-machine-arn ${resourceId} --region ${region}`;
      
      case 'stepfunctions_activity':
        return `aws stepfunctions delete-activity --activity-arn ${resourceId} --region ${region}`;
      
      // ===== EventBridge =====
      case 'eventbridge_rule':
      case 'events_rule':
        return `aws events delete-rule --name ${resourceName} --region ${region}`;
      
      case 'event_bus':
        return `aws events delete-event-bus --name ${resourceName} --region ${region}`;
      
      // ===== Backup =====
      case 'backup_vault':
        return `aws backup delete-backup-vault --backup-vault-name ${resourceName} --region ${region}`;
      
      case 'backup_plan':
        return `aws backup delete-backup-plan --backup-plan-id ${resourceId} --region ${region}`;
      
      // ===== SageMaker =====
      case 'sagemaker_endpoint':
        return `aws sagemaker delete-endpoint --endpoint-name ${resourceName} --region ${region}`;
      
      case 'sagemaker_endpoint_config':
        return `aws sagemaker delete-endpoint-config --endpoint-config-name ${resourceName} --region ${region}`;
      
      case 'sagemaker_model':
        return `aws sagemaker delete-model --model-name ${resourceName} --region ${region}`;
      
      case 'sagemaker_notebook_instance':
        return `aws sagemaker delete-notebook-instance --notebook-instance-name ${resourceName} --region ${region}`;
      
      // ===== Redshift =====
      case 'redshift_cluster':
        return `aws redshift delete-cluster --cluster-identifier ${resourceName} --skip-final-cluster-snapshot --region ${region}`;
      
      case 'redshift_subnet_group':
        return `aws redshift delete-cluster-subnet-group --cluster-subnet-group-name ${resourceName} --region ${region}`;
      
      // ===== Athena =====
      case 'athena_workgroup':
        return `aws athena delete-work-group --work-group ${resourceName} --recursive-delete-option --region ${region}`;
      
      case 'athena_named_query':
        return `aws athena delete-named-query --named-query-id ${resourceId} --region ${region}`;
      
      // ===== Cognito =====
      case 'cognito_user_pool':
        return `aws cognito-idp delete-user-pool --user-pool-id ${resourceId} --region ${region}`;
      
      case 'cognito_identity_pool':
        return `aws cognito-identity delete-identity-pool --identity-pool-id ${resourceId} --region ${region}`;
      
      // ===== AppSync =====
      case 'appsync_graphql_api':
        return `aws appsync delete-graphql-api --api-id ${resourceId} --region ${region}`;
      
      // ===== Amplify =====
      case 'amplify_app':
        return `aws amplify delete-app --app-id ${resourceId} --region ${region}`;
      
      // ===== WAF =====
      case 'waf_web_acl':
        return `aws wafv2 delete-web-acl --name ${resourceName} --scope ${resource.properties?.scope || 'REGIONAL'} --id ${resourceId} --lock-token ${resource.properties?.lockToken} --region ${region}`;
      
      case 'waf_rule_group':
        return `aws wafv2 delete-rule-group --name ${resourceName} --scope ${resource.properties?.scope || 'REGIONAL'} --id ${resourceId} --lock-token ${resource.properties?.lockToken} --region ${region}`;
      
      // ===== Default =====
      default:
        return `# Unknown resource type: ${resource.type} - manual deletion required`;
    }
  };

  // Fetch deletion plan from backend
  const fetchDeletionPlan = async () => {
    setLoading(true);
    setIsStateFileEmpty(false); // Reset empty state flag
    try {
      const response = await apiService.getDeletionPlan();
      setDeletionPlan(response);
      const script = generateBashScript(response);
      setBashScript(script);
      setBackendAvailable(true);
      message.success('Deletion plan loaded successfully');
    } catch (error) {
      console.error('Failed to fetch deletion plan:', error);
      
      if (error.message === 'Failed to fetch' || error.status === 0) {
        setBackendAvailable(false);
        message.error('Backend server not available');
      } else {
        message.error('Failed to load deletion plan');
      }
    } finally {
      setLoading(false);
    }
  };

  // Copy script to clipboard
  const handleCopyScript = () => {
    navigator.clipboard.writeText(bashScript).then(() => {
      message.success('Script copied to clipboard!');
    }).catch(() => {
      message.error('Failed to copy script');
    });
  };

  // Show clean all resources modal
  const showCleanModal = () => {
    setIsCleanModalVisible(true);
    setConfirmText('');
  };

  // Handle clean all resources
  const handleCleanAllResources = async () => {
    if (confirmText !== 'DELETE ALL') {
      message.error('Please type "DELETE ALL" to confirm');
      return;
    }

    setIsCleaningResources(true);
    try {
      const response = await apiService.emptyResources();
      
      // Handle new API response format
      if (response.success) {
        message.success(response.message || 'Successfully cleared all resources and dependencies');
        setIsStateFileEmpty(true); // Set success flag
        setDeletionPlan(null); // Clear deletion plan
        setBashScript('# No resources to delete'); // Reset script
      } else {
        message.warning('Operation completed but response format unexpected');
      }
      
      setIsCleanModalVisible(false);
      setConfirmText('');
    } catch (error) {
      console.error('Failed to empty state:', error);
      message.error(error.message || 'Failed to empty state file');
    } finally {
      setIsCleaningResources(false);
    }
  };

  // Cancel clean modal
  const handleCancelCleanModal = () => {
    setIsCleanModalVisible(false);
    setConfirmText('');
  };

  return (
    <div style={{ padding: '24px', maxWidth: '1400px', margin: '0 auto' }}>
      <PageHeader
        title="Resource Deletion"
        subtitle="Generate safe deletion scripts for your AWS infrastructure"
      />

      {/* Backend Offline Warning */}
      {!backendAvailable && (
        <Alert
          message="Backend Server Not Available"
          description="The backend server (http://localhost:8080) is not responding. Please start the backend server to use this feature."
          type="error"
          showIcon
          style={{ marginBottom: 16 }}
          closable
        />
      )}

      {/* Safety Warning */}
      <Alert
        message="⚠️ Important Safety Information"
        description={
          <div>
            <Paragraph style={{ marginBottom: 8 }}>
              <strong>This page generates deletion scripts only.</strong> The AI Agent does not have permission to delete resources for safety reasons.
            </Paragraph>
            <ul style={{ marginBottom: 0, paddingLeft: 20 }}>
              <li>Resources are ordered by dependency</li>
              <li>Review the generated script carefully before execution</li>
              <li>Test in a non-production environment first</li>
              <li>Ensure you have proper backups</li>
              <li>Some resources may require manual cleanup</li>
            </ul>
          </div>
        }
        type="warning"
        showIcon
        icon={<WarningOutlined />}
        style={{ marginBottom: 24 }}
      />

      <Space direction="vertical" size="large" style={{ width: '100%' }}>
        {/* Generate Script Section - Always Visible at Top */}
        <ScriptDisplayCard 
          script={bashScript}
          onCopy={handleCopyScript}
          onGenerate={fetchDeletionPlan}
          loading={loading}
        />

        {loading ? (
          <Card>
            <div style={{ textAlign: 'center', padding: '40px' }}>
              <Spin size="large" />
              <div style={{ marginTop: 16 }}>
                <Text type="secondary">Loading deletion plan...</Text>
              </div>
            </div>
          </Card>
        ) : deletionPlan ? (
          <>
            {/* Deletion Summary */}
            <Card title={<><InfoCircleOutlined /> Deletion Summary</>}>
            <Space direction="vertical" size="middle" style={{ width: '100%' }}>
              <div style={{ display: 'flex', gap: 24, flexWrap: 'wrap' }}>
                <SummaryStatCard 
                  label="Total Resources" 
                  value={deletionPlan.totalResources}
                  valueStyle={{ color: '#1890ff' }}
                />
                <SummaryStatCard 
                  label="Region" 
                  value={deletionPlan.region || 'N/A'}
                />
                <SummaryStatCard 
                  label="Generated At" 
                  value={new Date(deletionPlan.generatedAt).toLocaleString()}
                  valueStyle={{ fontSize: 16, fontWeight: 500 }}
                />
              </div>

              {deletionPlan.warnings && deletionPlan.warnings.length > 0 && (
                <Alert
                  message="Warnings"
                  description={
                    <ul style={{ marginBottom: 0, paddingLeft: 20 }}>
                      {deletionPlan.warnings.map((warning, index) => (
                        <li key={index}>{warning}</li>
                      ))}
                    </ul>
                  }
                  type="warning"
                  showIcon
                />
              )}

              <Divider style={{ margin: '12px 0' }} />

              <DeletionOrderTags 
                deletionOrder={deletionPlan.deletionOrder}
                resources={deletionPlan.resources}
              />
            </Space>
          </Card>

          {/* Resource List */}
          <Card title={<><DeleteOutlined /> Resources to Delete</>}>
            <Space direction="vertical" size="middle" style={{ width: '100%' }}>
              {deletionPlan.resources.map((resource, index) => (
                <DeletionResourceCard 
                  key={resource.id}
                  resource={resource}
                  index={index}
                />
              ))}
            </Space>
          </Card>
          </>
        ) : null}

        {/* Empty State File Button - Show only when there are resources or after successful empty */}
        {(deletionPlan && deletionPlan.totalResources > 0) || isStateFileEmpty ? (
          <Card>
            <Space direction="vertical" size="middle" style={{ width: '100%' }}>
              {isStateFileEmpty ? (
                <Alert
                  message="✓ State File Empty"
                  description="All tracked resources have been successfully removed from the state file. You can now start fresh by generating a new deployment plan."
                  type="success"
                  showIcon
                />
              ) : (
                <>
                  <Alert
                    message="⚠️ Empty State File"
                    description="This will remove all tracked resources from the state file. The actual AWS resources will NOT be deleted."
                    type="warning"
                    showIcon
                  />
                  <Button 
                    danger
                    icon={<ClearOutlined />} 
                    onClick={showCleanModal}
                    disabled={!backendAvailable || loading}
                    size="large"
                    block
                  >
                    Empty State File
                  </Button>
                </>
              )}
            </Space>
          </Card>
        ) : null}
      </Space>

      {/* Empty State File Modal */}
      <ConfirmationModal
        visible={isCleanModalVisible}
        onOk={handleCleanAllResources}
        onCancel={handleCancelCleanModal}
        confirmText={confirmText}
        onConfirmTextChange={setConfirmText}
        loading={isCleaningResources}
        title="Confirm Empty State File"
        okText="Empty State File"
        requiredText="DELETE ALL"
        warningTitle="⚠️ DANGER: Irreversible Action"
        warningDescription={
          <div>
            <Paragraph style={{ marginBottom: 8 }}>
              This action will <strong>permanently delete ALL resources</strong> from your infrastructure state.
            </Paragraph>
            <ul style={{ marginBottom: 0, paddingLeft: 20 }}>
              <li>All tracked resources will be removed</li>
              <li>This action cannot be undone</li>
              <li>The actual AWS resources will NOT be deleted (only from tracking)</li>
              <li>You may need to manually clean up AWS resources afterwards</li>
            </ul>
          </div>
        }
      />
    </div>
  );
};

export default ResourceDeletionPage;
